// src/config/axios-api.js
import axios from "axios";

// สร้าง instance ปกติ (ยังไม่ fix baseURL)
const axiosApi = axios.create();

// interceptor จะไปดึงค่า config.json ก่อน request จริง
axiosApi.interceptors.request.use(async (config) => {
    if (!config.baseURL) {
        const res = await fetch("/mra/config.json");
        const data = await res.json();

        // ค่าใน config.json เป็น array
        const api = data.find((c) => c.API_URL)?.API_URL;
        config.baseURL = api;
    }
    return config;
});

export default axiosApi;
